// Typography
//
// Headings, body text, lists, and other misc typographic elements.

h1, h2, h3, h4, h5, h6 {
  margin-bottom: .5rem;
  font-weight: 600;
  line-height: 1.25;
  color: var(--heading-color);
}

h1 {
  font-size: 2rem;
}

h2 {
  margin-top: 1rem;
  font-size: 1.5rem;
}

h2[id] {
  scroll-margin-top: 70px; /* masthead ë†’ì´ë³´ë‹¤ ì‚´ì§ ì—¬ìœ  ìˆê²Œ */
}


@media (max-width: 600px) {
  .page-title, .post-title, item.title, h1, h2, h3, h4, h5, h6 {
    text-align: center;
    word-break: keep-all;      /* ë‹¨ì–´ë¥¼ ê°€ëŠ¥í•œ í•œ ìœ ì§€ */
    white-space: normal;       /* ìì—°ìŠ¤ëŸ½ê²Œ ì¤„ë°”ê¿ˆ */
    overflow-wrap: break-word; /* ë‹¨ì–´ê°€ ë„ˆë¬´ ê¸¸ë©´ ë‹¨ì–´ ë‹¨ìœ„ë¡œ ì¤„ë°”ê¿ˆ */
    line-height: 1.4;
  }

  h2[id] {
    scroll-margin-top: 80px !important; /* ëª¨ë°”ì¼ í™˜ê²½ì—ì„œëŠ” ë” ì—¬ìœ ë¡­ê²Œ */
  }
}

h3 {
  margin-top: 1.5rem;
  font-size: 1.25rem;
}

h4, h5, h6 {
  margin-top: 1rem;
  font-size: 1rem;
}

p {
  margin-top: 0;
  margin-bottom: 1rem;
}

p_lowBtMargin {
  margin-top: 0;
  margin-bottom: 0.25rem;
  font-weight: bold;
}

ul, ol, dl {
  margin-top: 0;
  margin-bottom: 1rem;
}

dt {
  font-weight: bold;
}

dd {
  margin-bottom: .5rem;
}

hr {
  position: relative;
  margin: 0.5rem 0;
  // margin: var(--spacer-2) 0;
  border: 0;
  border-top: 1px solid var(--border-color);
}

abbr {
  font-size: 85%;
  font-weight: bold;
  color: var(--gray-600);
  text-transform: uppercase;

  &[title] {
    cursor: help;
    border-bottom: 1px dotted var(--border-color);
  }
}

blockquote {
  padding: .5rem 1rem;
  margin: .8rem 0;
  color: var(--gray-500);
  border-left: .25rem solid var(--border-color);

  p:last-child {
    margin-bottom: 0;
  }

  @media (min-width: 30em) {
    padding-right: 5rem;
    padding-left: 1.25rem;
  }
}

figure {
  margin: 0;
}

